.detail__img {
  height: 240px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.gradation__header {
  width: 100%;
  height: 50%;
  position: absolute;
  left: 0px;
  bottom: 0px;
  background: linear-gradient(
    0deg,
    #202027 0%,
    rgba(32, 32, 39, 0.65) 50%,
    rgba(32, 32, 39, 0) 100%
  );
}

.gradation__footer {
  width: 100%;
  height: 50%;
  position: absolute;
  left: 0px;
  top: 0px;
  background: linear-gradient(
    180deg,
    #202027 0%,
    rgba(32, 32, 39, 0.65) 50%,
    rgba(32, 32, 39, 0) 100%
  );
}

.detail__info {
  margin: 20px 20px 40px 20px;
}

.detail__title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 15px;
}

.detail__subinfo > p,
strong {
  font-size: 18px;
  font-weight: 400;
  color: #9e9ea4;
}

.genres::after {
  content: ", ";
}

.genres:last-child::after {
  content: none;
}

/* Review Section 시작 */

section.detail__review {
  margin: 0px 20px;
}

.review__wrap {
  width: calc(100% - 40px);
  padding: 20px;
  background-color: #3c3c47;
  box-shadow: 0 0 0 1px #4d4d59 inset;
  border-radius: 4px;
}

.review__wrap:last-child {
  margin: 30px auto 40px auto;
}

.user__wrap {
  display: flex;
  align-items: center;
}

.user__profile {
  border-radius: 50%;
  margin-right: 15px;
}

.user__rate {
  display: flex;
  align-items: center;
  margin-left: 4px;
  font-size: 15px;
  font-weight: 400;
  color: #9e9ea4;
}

.review__wrap > header {
  margin: 15px auto 5px auto;
  font-size: 18px;
  font-weight: 500;
}

.review__wrap > p {
  font-size: 15px;
  font-weight: 400;
  color: #e4e4e5;
}

.preview__link {
  width: 200px;
  height: 56px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  padding: 0px;
  margin: 30px auto 50px auto;
  color: #ffcd1a;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  background-color: #2c2c35;
}
