* {
  margin: 0;
  padding: 0;
  color: #fff;
  font-family: "Noto Sans KR", sans-serif;
}

html,
body {
  width: 100%;
  height: 100%;
  background-color: #202027;
}

.nav__title {
  font-size: 28px;
  font-weight: 700;
  margin: 30px auto 0px 20px;
}

h3 {
  margin: 0px auto 10px 0px;
  font-size: 18px;
  font-weight: 500;
}

section.highmovies {
  border-top: 1px solid #4d4d59;
  border-bottom: 1px solid #4d4d59;
  background-color: #3c3c47;
  margin: 20px 0px;
  padding: 20px 0px;
}

.highmovies__item__wrap {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: scroll;
  white-space: nowrap;
  scrollbar-width: none;
  -ms-overflow-style: none;
  border-radius: 4px;
}

.highmovies__item__wrap::-webkit-scrollbar {
  display: none;
}

.highmovies__item {
  padding: 0px 20px;
  display: flex;
  border-radius: 4px;
}

.honor__title {
  margin-left: 20px;
}

.movies {
  padding-bottom: 20px;
}